/* NAVBAR */
.login-out{
  cursor:pointer;
}

.login-out {
    position: relative;
    cursor: pointer;
}

.login-out:before, .login-out:after {
    content: '';
    position: absolute;
    width: 0%;
    height: 2px;
    top: 50%;
    margin-top: -0.5px;
    background: #fff;
}

.login-out:before {
    left: -2.5px;
}
.login-out:after {
    right: 2.5px;
    background: red;
    transition: width 0.8s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.login-out:hover:before {
    background: red;
    width: 100%;
    transition: width 0.5s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.login-out:hover:after {
    background: transparent;
    width: 100%;
    transition: 0s;
}




.login-out:hover{
  color:grey;
  transition:0.1s;
}

