/* ZONAS */

.enemy{
  width: 20%;
  -webkit-filter: drop-shadow(3px 3px 0 red)
                  drop-shadow(-3px -3px 0 red);
  filter: drop-shadow(3px 3px 0 red)
          drop-shadow(-3px -3px 0 red);
  cursor:crosshair;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.e1{
  animation:Grow 5s;
}

.e2{
  animation:MoveRight 3s;
}

.e3{
  animation:Shrink 4s;
}

.e4{
  animation:MoveLeft 2s;
}

.e5{
  animation:MoveLeftRight 4s;
}

.damage{
  margin-left: 200px;
