}
.bg-brown{
  background-color: brown;
  padding: 2px 2px;
  font-size:12px;
  white-space: nowrap
}

/* ANIMATIONS */

@keyframes fadeIn {
  from {
    opacity:0
  }
  to {
    opacity:1
  }
}


@keyframes Color {
  from {
    filter:grayscale(100%)
  }
  to {
    filter:grayscale(0%)
  }
}

@keyframes Up {
  from {
    transform:rotate(-1deg);
  }
  to {
    transform:rotate(3deg);
  }
}

@keyframes Grow{
  from {
    transform:scale(0.5);
  }
  to {
    transform:scale(1.2);
  }
}
@keyframes Shrink{
  from {
    transform:scale(1.5);
  }
  to {
    transform:scale(1);
  }
}
@keyframes MoveLeft{
  from {
    transform:translateX(-50px);
  }
  to {
    transform:translateX(0);
  }
}
@keyframes MoveRight{
  from {
    transform:translateX(50px);
  }
  to {
    transform:translateX(0);
  }
}

@keyframes MoveLeftRight{
  from {
    transform:translateX(50px);
  }
  to {
    transform:translateX(-100px);
  }
}

@keyframes UpShow {
  from {
    transform:translateY(-10px);
    opacity:0;
  }
  to {
    transform:translateY(-40px);
    opacity:1;
  }
}


@keyframes TrueUp {
  from {
    transform:translateY(10px);
    opacity:0;
  }
  to {
    transform:translateY(0px);
    opacity:1;
  }
}
